.container {
padding:5vh;
height:100vh;
background-color:#f0f0f5;
}

h1, h2, .result{
	color:#1f2e2e;
}

h1 {
 font-size:2em;
 padding:5%;
}

h2 {
 padding-bottom:1em;
}

.temp{
	margin:0 auto;
	width:12em;
	height:12em;
	border:1px dashed tomato;
	border-radius:100%;
}

.result{
	font-size:2em;
	padding:2.5em 1em 0.5em 1em;
}

.ui.button.primary{
	margin-top:2em;
	background-color:tomato;
}

.ui.button.primary:hover, .ui.button.primary:active{
	background-color:#1f2e2e;
}

 